<template>
    <div class="m-publish-box">
        <!--  -->
        <publish-header name="">
            <slot name="header"></slot>
        </publish-header>

        <el-form class="m-publish-post">
            <div class="m-publish-source">
                <el-divider content-position="left"> *</el-divider>
                <el-radio-group class="m-publish-action" v-model="action">
                    <el-radio-button label="new" :disabled="isEditMode"></el-radio-button>
                    <el-radio-button label="update"></el-radio-button>
                </el-radio-group>

                <!--  -->
                <el-input class="u-current-source" v-if="isEditMode" v-model="currentSource" disabled></el-input>

                <!--  -->
                <template v-else>
                    <template v-if="action == 'new'">
                        <!--  -->
                        <div class="u-create-source">
                            <el-select class="u-source-type" placeholder="" v-model="knowledge.type">
                                <el-option
                                    v-for="type in types"
                                    :key="type.name"
                                    :value="type.name"
                                    :label="type.label"
                                ></el-option>
                            </el-select>
                            <el-input
                                class="u-source-name"
                                placeholder=""
                                v-model="knowledge.name"
                            ></el-input>
                        </div>
                        <p class="u-source-add-tip">
                            <span>Note</span>
                        </p>
                    </template>
                    <template v-else>
                        <!--  -->
                        <el-select
                            class="u-source-id"
                            v-model.lazy="post.source_id"
                            filterable
                            remote
                            placeholder=""
                            :remote-method="loadSources"
                            :loading="options.loading"
                            clearable
                            @change="selectSource"
                        >
                            <el-option
                                v-for="item in options.sources"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"
                            >
                                <div class="u-option">
                                    <span class="u-type">{{ showType(item.type) }}</span>
                                    <span class="u-name" v-text="item.name"></span>
                                </div>
                            </el-option>
                        </el-select>
                    </template>
                </template>
            </div>

            <div class="m-publish-remark">
                <el-divider content-position="left"> *</el-divider>
                <el-input
                    v-model="post.remark"
                    :maxlength="200"
                    :minlength="1"
                    show-word-limit
                    required
                    placeholder=""
                ></el-input>
            </div>

            <div class="m-publish-content">
                <el-divider content-position="left"> *</el-divider>
                <Tinymce v-model="post.content" :attachmentEnable="true" :resourceEnable="true" :height="600">
                    <el-alert type="warning" class="m-latest-check" show-icon v-if="!isLatest && latest.post && current.post">
                        <template #title>
                            <span class="u-alert-title"></span>
                            <el-link type="primary" icon="el-icon-link" :href="getLink(post.source_id)" target="_blank" class="u-view-latest"></el-link>
                            <el-link @click="getLatest" icon="el-icon-download" class="u-get-latest" type="primary" v-if="latest.post"></el-link>
                        </template>
                    </el-alert>
                </Tinymce>
            </div>

            <div class="m-publish-tags">
                <el-divider content-position="left"></el-divider>
                <el-tag
                    :key="key"
                    v-for="(tag, key) in post.tags"
                    closable
                    :disable-transitions="false"
                    @close="post.tags.splice(key, 1)"
                    >{{ tag }}
                </el-tag>
                <el-input
                    class="input-new-tag"
                    v-if="inputVisible"
                    v-model="inputValue"
                    ref="saveTagInput"
                    size="small"
                    placeholder=""
                    @keyup.enter.native="handleInputConfirm"
                    @blur="handleInputConfirm"
                >
                </el-input>
                <el-button v-else class="button-new-tag" size="small" @click="showInput">+  </el-button>
            </div>

            <div class="m-publish-commit">
                <el-divider content-position="left"></el-divider>
                <el-button
                    class="u-publish"
                    icon="el-icon-s-promotion"
                    type="primary"
                    @click="handleSubmit"
                    :disabled="processing"
                    >
                </el-button>
            </div>
        </el-form>
    </div>
</template>

<script>
import header from "@/components/publish_header.vue";
import Tinymce from "@jx3box/jx3box-editor/src/Tinymce";

// 
// import { WikiPost } from "@jx3box/jx3box-common/js/helper";
import { wiki } from "@jx3box/jx3box-common/js/wiki_v2";
import User from "@jx3box/jx3box-common/js/user";
import { get_menus, get_list, create_knowledge } from "../service/knowledge";
import { iconLink, getLink } from "@jx3box/jx3box-common/js/utils";

export default {
    name: "knowledge",
    data() {
        return {
            // 
            // ================
            action: "new",
            isEditMode: false,

            // 
            // =================
            // 
            types: [],
            options: {
                sources: [],
                loading: false,
            },
            // 
            knowledge: {
                type: "",
                name: "",
            },
            // 
            currentSource: "",

            // 
            // =================
            processing: false,
            post: {
                id: "",
                content: "",
                source_id: "",
                remark: "",
                tags: [],
                client: ""
            },

            // 
            // =================
            // 
            inputVisible: false,
            inputValue: "",

            latest: {},
            current: {}
        };
    },
    computed: {
        client: function () {
            return this.$store.state.client;
        },
        id : function (){
            return this.$route.query?.post_id
        },
        // 
        isLatest : function (){
            if(!this.current?.post?.id || !this.latest?.post?.id) return false
            return this.current.post.id == this.latest.post.id
        },
    },
    methods: {
        // 
        loadData: function (id) {
            if (!id) return;
            wiki.get({ type: "knowledge", id }, { client: 'all' }).then((res) => {
                // 
                let source = res.data?.data?.source;
                this.currentSource = source?.name;
                this.post.source_id = source?.id;

                this.latest = res.data.data

                // 
                let post = res.data?.data?.post;
                this.post.remark = post?.remark;
                this.post.content = post?.content || "";
                this.post.tags = post?.tags?.split(",") || [];
            });
        },
        loadDataByPostId() {
            return wiki.getById(this.id).then(res => {
                let source = res.data?.data?.source;
                this.currentSource = source?.name;
                this.post.source_id = source?.id;

                this.current = res.data.data
                this.post.content = this.current.post.content
                this.post.remark = this.current.post.remark
                this.post.tags = this.current.post.tags.split(',') || []
            })
        },
        // 
        loadTypes: function () {
            get_menus().then((res) => {
                this.types = res.data.data;
            });
        },
        // 
        loadSources(keyword = "") {
            this.options.loading = true;
            get_list({
                _search: keyword,
                per: 10,
            })
                .then((res) => {
                    this.options.sources = res.data.data.list;
                })
                .finally(() => {
                    this.options.loading = false;
                });
        },
        // 
        resetSource: function () {
            if (this.action == "new") {
                // 
                this.post.source_id = "";
                this.currentSource = "";

                // 
                this.post.content = "";
                this.post.tags = [];
                this.post.remark = "";
            }
        },

        // 
        // ======================
        handleSubmit: function () {
            if (this.action == "new" && !this.post.source_id) {
                this.createSource().then(() => {
                    // 
                    this.createPost();
                });
            } else {
                this.createPost();
            }
        },

        // 1.
        createSource() {
            this.processing = true;
            return create_knowledge({
                type: this.knowledge.type,
                name: this.knowledge.name,
            })
                .then((res) => {
                    this.post.source_id = res.data.data.id;
                    // this.$message({
                    //     message: `${res.message}`,
                    //     type: "warning",
                    // });
                })
                .finally(() => {
                    this.processing = false;
                });
        },

        // 2.
        selectSource: function (source_id) {
            this.loadData(source_id);
        },

        // 3.
        validate() {
            let result = ["source_id", "content", "remark"].every((prop) => {
                return !!this.post[prop];
            });
            if (!result) this.$message.error("");
            return result;
        },
        // 4.
        createPost: function () {
            // 
            if (!this.validate()) return;
            this.processing = true;
            const data = {
                source_id: String(this.post.source_id),
                user_nickname: User.getInfo().name,
                content: this.post.content,
                remark: this.post.remark,
                tags: this.post.tags.join(","),
            };
            wiki.post({ type: "knowledge", ...data, client: "all" })
                .then(() => {
                        this.$message({ message: "", type: "success" });
                        setTimeout(() => {
                            this.$router.push({ name: "wiki_post", params: { type: "knowledge" } });
                        }, 500);
                })
                .finally(() => {
                    this.processing = false;
                });
        },

        // 
        // =========================
        handleInputConfirm() {
            let inputValue = this.inputValue.trim();
            if (inputValue) {
                this.post.tags.push(inputValue);
            }
            this.inputVisible = false;
            this.inputValue = "";
        },
        showInput() {
            this.inputVisible = true;
            this.$nextTick((_) => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        showType(val) {
            return this.types.find((item) => {
                return item.name == val;
            }).label;
        },
        getLink(id) {
            return getLink('knowledge', id)
        },
        getLatest() {
            this.post.content = this.latest.post?.content || ''
        }
    },
    watch: {
        // 
        "$route.params.source_id": {
            immediate: true,
            handler: function (val) {
                // 
                if (val) {
                    this.action = "update";
                    this.isEditMode = true;
                    this.post.source_id = ~~val;
                    this.loadData(val);

                    if (this.id) {
                        this.loadDataByPostId()
                    }
                    // 
                } else {
                    this.action = "new";
                    // 
                    this.loadTypes();
                    // 
                    this.loadSources();
                }
            },
        },
        // action
        action: {
            handler: function () {
                if (!this.isEditMode) {
                    this.resetSource();
                }
            },
        },
    },
    components: {
        "publish-header": header,
        Tinymce,
    },
};
</script>

<style scoped lang="less">
@import "../assets/css/knowledge.less";
@import "../assets/css/wiki.less";
</style>
